<template>
  <div id="app">
    <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/sockets">Fake Sockets</router-link> |
      <router-link to="/sockets2">Test Sockets</router-link>
    </div>
    <router-view/>
  </div>
</template>

<script>
import { mapActions, mapMutations } from 'vuex';

export default {
  methods: {
    ...mapActions({
      recieveMessage: 'recieveMessage',
      recieveMessage2: 'recieveMessage2',
      connection: 'connection',
      connection2: 'connection2'
    }),
    ...mapMutations({
      setError2: 'setError2'
    })
  },
  created() {
    this.$SocketService.connect();
    this.$socket.onopen = evt => {
      console.log('FAKE SOCKET CONNECTED')
      this.connection();
    };
    this.$socket2.onopen = evt => {
      console.log('TEST SOCKET CONNECTED')
      this.connection2();
    };
    this.$socket.onmessage = evt => {
      this.recieveMessage(evt.data);
    };
    this.$socket2.onmessage = evt => {
      this.recieveMessage2(evt.data);
    };
    this.$socket.onerror = evt => {
      console.log('FAKE SOCKET ERROR')
    };
    this.$socket2.onerror = evt => {
      console.log('TEST SOCKET ERROR')
      this.setError2();
    };
    this.$socket.onclose = evt => {
      console.log('FAKE SOCKET CLOSED')
    }
    this.$socket2.onclose = evt => {
      console.log('TEST SOCKET CLOSED')
    }
  },
}
</script>

