const url = 'wss://echo.websocket.org';
const url2 = 'ws://127.0.0.1:8083';
export default {
  install(Vue) {
    // сервис для подписки через сокет
    // connect - подписаться
    // stop - отписаться
    // $socket - позволяет настроить обработку получаемых данных и работать с каналом из компонента
    Vue.prototype.$SocketService = {
      connect() {
        Vue.prototype.$socket = new WebSocket(url);
        Vue.prototype.$socket2 = new WebSocket(url2);
      },
      stop(name) {
        Vue.prototype[name].close(1000);
      }
    }
  }
}