<template>
  <div class="home">
    <div class="wrapper">
      <div style="height: 500px; width: 300px; border-radius: 20px; overflow: hidden">
        <l-map ref="myMap" @ready="doSomethingOnReady()" 
        style="margin: 0 auto"
        :zoom="zoom"
        :center="center"
        @update:zoom="zoomUpdated"
        @update:center="centerUpdated"
        @update:bounds="boundsUpdated"
      >
        <l-tile-layer :url="url"></l-tile-layer>
        <l-marker :lat-lng="markerLatLng" ></l-marker>
      </l-map>
      </div>
      <div style="width: 300px; text-align: center;">
        <img alt="Vue logo" style="display: block; margin: 20px auto" width="100" src="../assets/logo.png">
        <div>
          <button @click="doFake">Fake GET</button>
          <button @click="doTest">Test GET</button>
          <button @click="doTest2">Test GET (axios)</button>
          <button @click="doTest3">Fake POST (axios)</button>
          <button @click="doTest4">Test POST (axios)</button>
        </div>
        <div v-if="loading" style="margin-top: 15px">LOADING...</div>
        <p ref="fakeRes" style="padding-left: 20px" v-show="gotFake"></p>
        <p ref="testRes" v-show="gotTest"></p>
      </div>
    </div> 
  </div>
</template>

<script>
import L from 'leaflet';
import { Icon } from 'leaflet';
import { LMap, LTileLayer, LMarker } from 'vue2-leaflet';
const axios = require('axios');

delete Icon.Default.prototype._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
});

export default {
  name: 'Home',
  components: {
    LMap,
    LTileLayer,
    LMarker,
  },
  data() {
    return {
      gotFake: false,
      gotTest: false,
      loading: false,
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      zoom: 16,
      center: [59.826220, 30.141282],
      markerLatLng: [59.826220, 30.141282],
      bounds: null
    }
  },
  methods: {
    doSomethingOnReady() {
        this.map = this.$refs.myMap.mapObject
    },
    zoomUpdated (zoom) {
      this.zoom = zoom;
    },
    centerUpdated (center) {
      this.center = center;
    },
    boundsUpdated (bounds) {
      this.bounds = bounds;
    },
    doFake() {
      let status = '';
      this.gotFake = false;
      this.gotTest = false;
      this.loading = true;
      fetch('https://jsonplaceholder.typicode.com/users/1', {
        headers: {
          'Content-Type': 'application/json;charset=utf-8'
        }
      })
        .then(response => {
          console.log(response)
          status = response.status;
          return response.json()
          })
        .then(json => {
          this.loading = false;
          console.log(json)
          this.$refs.fakeRes.innerHTML = `<b>status: </b>${status}<br><b>answer:</b> ${json.name}`;
          this.gotFake = true;
        })
        .catch(() => {
          this.loading = false;
          this.$refs.fakeRes.innerHTML = `<span style='color: red'>ERROR FAKE</span>`;
          this.gotFake = true;
        })
    },
    doTest() {
      let status = '';
      this.gotFake = false;
      this.gotTest = false;
      this.loading = true;
      fetch('http://127.0.0.1:8082/test')
        .then(response => {
          console.log(response)
          status = response.status;
          return response.json()
          })
        .then(json => {
          this.loading = false;
          console.log(json)
          this.$refs.testRes.innerHTML = `<b>status: </b>${status}`;
          this.gotTest = true;
        })
        .catch(() => {
          this.loading = false;
          this.$refs.testRes.innerHTML = `<span style='color: red'>ERROR TEST</span>`;
          this.gotTest = true;
        })
    },
    doTest2() {
      let status = '';
      this.gotFake = false;
      this.gotTest = false;
      this.loading = true;
      axios.get('http://127.0.0.1:8082/test')
        .then(response => {
          console.log(response)
          this.loading = false;
          status = response.status;
          this.$refs.testRes.innerHTML = `<b>status: </b>${status}`;
          this.gotTest = true;
        })
        .catch(e => {
          console.log(e)
          this.loading = false;
          this.$refs.testRes.innerHTML = `<span style='color: red'>ERROR TEST</span>`;
          this.gotTest = true;
        })
        .then(() => console.log('AXIOS'))
    },
    doTest3() {
      let status = '';
      this.gotFake = false;
      this.gotTest = false;
      this.loading = true;
      fetch('https://jsonplaceholder.typicode.com/posts', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify({age: 42})
      }).then(res => console.log(res))
      // axios.post('https://jsonplaceholder.typicode.com/posts', {
      //   why: '42'
      // })
      //   .then(response => {
      //     console.log(response)
      //     this.loading = false;
      //     status = response.status;
      //     this.$refs.testRes.innerHTML = `<b>status: </b>${status}`;
      //     this.gotTest = true;
      //   })
      //   .catch(e => {
      //     console.log(e)
      //     this.loading = false;
      //     this.$refs.testRes.innerHTML = `<span style='color: red'>ERROR TEST</span>`;
      //     this.gotTest = true;
      //   })
      //   .then(() => console.log('AXIOS POST'))
    },
    doTest4() {
      let status = '';
      this.gotFake = false;
      this.gotTest = false;
      this.loading = true;
      axios.post('http://127.0.0.1:8082/post', {
        why: '42'
      })
        .then(response => {
          console.log(response)
          this.loading = false;
          status = response.status;
          this.$refs.testRes.innerHTML = `<b>status: </b>${status}`;
          this.gotTest = true;
        })
        .catch(e => {
          console.log(e)
          this.loading = false;
          this.$refs.testRes.innerHTML = `<span style='color: red'>ERROR TEST</span>`;
          this.gotTest = true;
        })
        .then(() => console.log('AXIOS POST'))
    }
  }
}
</script>

<style lang="scss" scoped>
  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 500px;
    margin: 0 auto;
  }
</style>
