import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import socketPlugin from './plugins/socketService'
import 'leaflet/dist/leaflet.css';

Vue.config.productionTip = false

Vue.use(socketPlugin);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
