import Vue from 'vue'
import Vuex from 'vuex'

let $socket;
let $socket2;
setTimeout(() => {
  while(!Vue.prototype.$socket && !Vue.prototype.$socket2) {
  }
  $socket = Vue.prototype.$socket;
  $socket2 = Vue.prototype.$socket2;

}, 0)


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    connection: false,
    connection2: false,
    isTestRecieved: false,
    isSettingsRecieved: false,
    socketMsg: '',
    socketMsg2: '',
    error2: false,
  },
  getters: {
    isConnected: state => state.connection,
    isConnected2: state => state.connection2,
    isTestRecieved: state => state.isTestRecieved,
    isSettingsRecieved: state => state.isSettingsRecieved,
    getSocketMsg: state => state.socketMsg,
    getSocketMsg2: state => state.socketMsg2,
    getError2: state => state.error2,
  },
  mutations: {
    setConnection(state) {
      state.connection = true;
    },
    setConnection2(state) {
      state.connection2 = true;
    },
    setTest(state) {
      state.isTestRecieved = true;
    },
    setSettings(state) {
      state.isSettingsRecieved = true;
    },
    setSocketMsg(state, payload) {
      state.socketMsg = payload;
    },
    setSocketMsg2(state, payload) {
      state.socketMsg2 = payload;
    },
    setError2(state) {
      state.error2 = true;
    }
  },
  actions: {
    connection({commit}) {
      commit('setConnection');
    },
    connection2({commit}) {
      commit('setConnection2');
    },
    sendMessage(ctx, payload) {
      console.log('RUN sendMessage');
      $socket.send(payload);
    },
    sendMessage2(ctx, payload) {
      console.log('RUN sendMessage');
      $socket2.send(payload);
    },
    recieveMessage(ctx, payload) {
      console.log('MESSAGE RECIEVED')
      let answer;
      try {
        answer = JSON.parse(payload);
      } catch(e) {
        answer = payload;
      }
      switch (answer.type) {
        case 'test':
          ctx.dispatch('loadTest');
          break;
        case 'settings':
          ctx.dispatch('loadSettings');
          break;
        default:
          console.log(answer);
          ctx.dispatch('newMsg', payload);
      }
    },
    recieveMessage2(ctx, payload) {
      console.log('TEST MESSAGE RECIEVED')
      let answer;
      try {
        answer = JSON.parse(payload);
      } catch(e) {
        answer = payload;
      }
      ctx.dispatch('newMsg2', payload);
    },
    loadTest({commit}) {
      commit('setTest')
    },
    loadSettings({commit}) {
      commit('setSettings')
    },
    newMsg({commit, getters}, payload) {
      if (getters.isSettingsRecieved) {
        commit('setSocketMsg', payload);
      }
    },
    newMsg2({commit, getters}, payload) {
      commit('setSocketMsg2', payload);
    }
  },
  modules: {
  }
})
